@import url(http://fonts.googleapis.com/css?family=Roboto);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/**
* Importing necessary  Styles.
**/
/*---- Fonts ----- */
;
/*______________________ Global Perfix ___________________*/
:root{
  --theme:#742ddd;
  --accent:#8890a3;
  --theme-light:#8953d8;
}
body {
 font-family:'Roboto', sans-serif;
 color:#8890a3;
 font-size: 15px;
 position: relative;
 width: -webkit-fit-content;
 width: -moz-fit-content;
 width: fit-content;
 overflow-x: hidden;
}
.main-page-wrapper {overflow-x:hidden;}
h1,h2,h3,h4,h5,h6,p,ul { margin:0;padding: 0;}
h1,h2,h3,h4,h5,h6 {color: #101530;}
h1 {font-family: 'Roboto'; font-size: 50px; line-height: 68px;}
h2 {
  font-family: 'Roboto';
  font-size: 44px;
  line-height: 62px;
}
h3 {font-size: 32px;}
h4 {font-size: 24px;}
h5 {font-size: 20px; font-weight: 600;}
h6 {font-size: 18px; font-weight: 600;}
p {line-height: 25px;}
ul {list-style-type: none;}
body a {text-decoration: none;color: #8890a3; display: inline-block;}
a:hover,a:focus,a:visited {text-decoration: none; outline: none;}
img {max-width: 100%; display: block;}
button {border:none;outline: none;box-shadow: none;display: block; padding: 0;cursor: pointer;}
button:focus {outline: none;}
input,textarea {outline: none; box-shadow: none;transition: all 0.3s ease-in-out;}
.section-spacing {padding-top: 130px;}


.darkbanner{
  background: rgba(255,255,255,0.8);
      /* Background image is centered vertically and horizontally at all times */
      background-position: center center;
  
      /* Background image doesn't tile */
      background-repeat: no-repeat;
      
      /* Background image is fixed in the viewport so that it doesn't move when 
         the content's height is greater than the image's height */
      background-attachment: fixed;
      
      /* This is what makes the background image rescale based
         on the container's size */
      background-size: cover;
      /* opacity: 0.6; */
      height: 40vh;
}
.darkbanner h2,.darkbanner h4{
  opacity: 1 !important;
  color: white;
}
.signup-banner a div{
  margin-top: 30px;
  margin-bottom: 20px;
  width: 100%;
  border-bottom: 1px solid !important;
  border-spacing: 50px !important;
  border-bottom-width: thick;
  
}
.signup-banner a:hover{
  opacity: 0.6;
  
}

.darkbanner{
  margin-top: 150px;
}
.signup-banner{
  margin-top: 200px !important;
  margin-bottom: 150px !important;
}
/* navigation */
.navlogo span a{
    color:#fff;
}
.navbar span{
    color:#fff;
}
.navbar .fa-bars {
    display: none;
    color: var(--accent) ;
    font-size: 1rem;
  }
.navbar div ul li{
    float: left;
    margin-right: 9px;
    list-style: none;
}
.navbar div ul li a {
    text-decoration: none;
    color: var(--accent) ;
    font-size: 12px;

}

.navbar div ul li a.active{
    
    color: #fff ;
}
.navright.btn{
    height:20px !important;
}
.float-right{
    position: absolute;
        right: 150px;
}

@media only screen and (max-width: 800px) {
  .navbar{
    position: absolute;
    width: 100%;
    z-index: 2;
  }
    .collapsenav {
        display: none;
    }
  
    .navlogo {
        width: 100%;
        display: block;
        margin: 0px;
        margin-left: -5px;
    }
    .navbar .fa-bars {
        display: inline-block;
        position: relative;
        /* right: 25px; */
        margin-left: -30px;
        top:5px;
        cursor: pointer;
        font-size: 25px;
        z-index: 99999;

      }
      .navright{
        display: block;
        position: absolute;
        right: 32px;
        
      }
      .navright ul li {
        position: absolute !important;
        display: block;
        top:2px;
        right:65px;

      }


      .is-expanded 
      
      {
          margin-top: 20px;
        padding-bottom: 125px;
        width:100%;
        
        /* padding-right:200px; */
        display: block;;
        overflow: hidden;
        max-height: 500px; /* approximate max height */
        transition-duration: 0.4s;
        transition-timing-function: cubic-bezier(0, 1, 0.5, 1);

        
        }
 
      .is-expanded ul li  {
        float: none !important;
        margin-bottom: 20px;
        border-bottom: 1px solid;
         
      }
      .is-expanded ul li a  {
        margin-left: 140px;

        
      }
      .float-right{
        /* float:none; */
        position:absolute;
width: 90%;
        right:auto;
        left:10px;        
        
    }
    .float-right li  {
        border-bottom:none !important;
        border-radius: 2px;
    }
    .float-right li a{
        margin-left: 140px  !important;
        font-size: 15px !important;
        
    }
        
  }
