/* navigation */
.navlogo span a{
    color:#fff;
}
.navbar span{
    color:#fff;
}
.navbar .fa-bars {
    display: none;
    color: var(--accent) ;
    font-size: 1rem;
  }
.navbar div ul li{
    float: left;
    margin-right: 9px;
    list-style: none;
}
.navbar div ul li a {
    text-decoration: none;
    color: var(--accent) ;
    font-size: 12px;

}

.navbar div ul li a.active{
    
    color: #fff ;
}
.navright.btn{
    height:20px !important;
}
.float-right{
    position: absolute;
        right: 150px;
}

@media only screen and (max-width: 800px) {
  .navbar{
    position: absolute;
    width: 100%;
    z-index: 2;
  }
    .collapsenav {
        display: none;
    }
  
    .navlogo {
        width: 100%;
        display: block;
        margin: 0px;
        margin-left: -5px;
    }
    .navbar .fa-bars {
        display: inline-block;
        position: relative;
        /* right: 25px; */
        margin-left: -30px;
        top:5px;
        cursor: pointer;
        font-size: 25px;
        z-index: 99999;

      }
      .navright{
        display: block;
        position: absolute;
        right: 32px;
        
      }
      .navright ul li {
        position: absolute !important;
        display: block;
        top:2px;
        right:65px;

      }


      .is-expanded 
      
      {
          margin-top: 20px;
        padding-bottom: 125px;
        width:100%;
        
        /* padding-right:200px; */
        display: block;;
        overflow: hidden;
        max-height: 500px; /* approximate max height */
        -moz-transition-duration: 0.4s;
        -webkit-transition-duration: 0.4s;
        -o-transition-duration: 0.4s;
        transition-duration: 0.4s;
        -moz-transition-timing-function: cubic-bezier(0, 1, 0.5, 1);
        -webkit-transition-timing-function: cubic-bezier(0, 1, 0.5, 1);
        -o-transition-timing-function: cubic-bezier(0, 1, 0.5, 1);
        transition-timing-function: cubic-bezier(0, 1, 0.5, 1);

        
        }
 
      .is-expanded ul li  {
        float: none !important;
        margin-bottom: 20px;
        border-bottom: 1px solid;
         
      }
      .is-expanded ul li a  {
        margin-left: 140px;

        
      }
      .float-right{
        /* float:none; */
        position:absolute;
width: 90%;
        right:auto;
        left:10px;        
        
    }
    .float-right li  {
        border-bottom:none !important;
        border-radius: 2px;
    }
    .float-right li a{
        margin-left: 140px  !important;
        font-size: 15px !important;
        
    }
        
  }
  @media only screen and (max-width: 600px) {

     
   

  }

